* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  font-family: 'Noto Sans CJK KR', sans-serif;
}

html {
  font-size: 16px;
}

body {
  background-color: var(--color-background-body);

  transition-duration: var(--time-long);
}

input, button, textarea {
  font-family: inherit;
  font-size: inherit;
}
